@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=RocknRoll+One&display=swap);
/* @import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300;400&display=swap'); */
:root {
  --primary-font: "Roboto", sans-serif;
  --secondary-font: 'RocknRoll One', sans-serif;
  --primary-color: rgb(79, 143, 79);
  --secondary-color: rgba(55, 99, 55, 1.0);
  --primary-text: rgb(255, 255, 255);
  --secondary-text: rgb(153, 209, 106);
  --danger: rgb(201, 73, 73);
  --success: rgb(0, 204, 27);
  --highlight: rgb(255, 142, 50);
  --light-shadow: 1px 1px 3px 0px rgba(0, 0, 0, .3);
  /* In case it needs to be adjusted, dynamically or otherwise */
  --topnav-height: 5rem;
  --row-height: 2.8rem;
}

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

html {
  font-size: 16px;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  margin: 0;
  font-family: "Roboto", sans-serif;
  font-family: var(--primary-font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(79, 143, 79);
  background: var(--primary-color);
  color: white;
}

main {
  /* max-height: calc(100vh - var(--topnav-height)); */
  max-height: 100%;
  padding-top: 5rem;
  padding-top: var(--topnav-height);
  background: rgb(79, 143, 79);
  background: var(--primary-color);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  animation: fade-in .3s backwards .3s;
  -webkit-animation: fade-in .3s backwards .3s;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

li, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p, h1, h2, h3, h4 {
  padding: 0;
  margin: 0;
  font-weight: 400;
}

b {
  font-weight: 500;
}

a {
  text-decoration: none;
  color: unset;
}

input, button {
  outline: none;
  border: none;
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  font-family: var(--primary-font);
}

input {
  text-indent: 8px;
}

button:active, button:focus {
  color: rgb(255, 142, 50);
  color: var(--highlight);
}

.app {
  height: 100vh;
  position: relative;
}

@media screen and (min-width: 32rem) {
  .app {
    width: 32rem;
    overflow: hidden;
    height: 95vh;
    margin: 1rem auto;
    border: solid 10px black;
    border-radius: 2rem;
  }
  ::-webkit-scrollbar {
    width: 6px;
  }
  ::-webkit-scrollbar-track {
    background: rgba(55, 99, 55, 1.0);
    background: var(--secondary-color);
  }
  ::-webkit-scrollbar-thumb {
    background-color: rgb(79, 143, 79);
    background-color: var(--primary-color);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
